import React, { Component } from 'react'
import { Modal, Row, Col, Table } from 'react-bootstrap'
import Button from '../../../components/button/button'
import Optimize from '../../../optimization/optimizationNumber/optimizationNumber'
import ReactToPrint from 'react-to-print';
import DatePicker from '../../../components/datePicker/datePicker'
import { get_data_print } from './expensesAction'
import Input from '../../../components/input/input';
import RadioButton from '../../../components/radioButton/radioButton'
import './expensePrint.css'
class ComponentToPrint extends React.Component {
  render() {
    return (
      <React.Fragment>
        {this.props.children}
      </React.Fragment>
    );
  }
}
class Confirm extends Component {
  static defaultProps = {
    show: false,

  }
  constructor(props) {
    super(props);
    var today = new Date()
    this.state = {
      show: this.props.show,
      printComponent: (<div></div>),
      tableData: {},
      selectedData: [],
      fromDay: new Date(today.getFullYear(),today.getMonth(),1),
      toDay: new Date(today.getFullYear(),today.getMonth(),5),
      lastDay: new Date(today.getFullYear(),today.getMonth(),10),
      selectType: '',
      admin: 'Phan Vn Thnh'
    }
  }
  getToday = () => {
    return new Date();
  }
  componentDidMount() {
    this.setState({
      tableData: this.props.printTable,
      selectedData: this.props.printSelected
    })
  }
  componentWillReceiveProps(props) {
    if (props.show !== this.state.show) {
      this.setState({ show: props.show })
    }
    if (props.printTable !== this.state.tableData) {
      this.setState({ tableData: {
        month: parseInt(props.printTable.month),
        year: parseInt(props.printTable.year),
        room: props.printTable.room,
        status: parseInt(props.printTable.status)
      } })
    }
    if (props.printSelected !== this.state.selectedData) {
      this.setState({ selectedData: props.printSelected })
    }
  }

  handleClose = () => {
    this.setState({ show: false, 
      printComponent: (<div></div>),
      selectType: '',
      admin: 'Phan Vn Thnh'
    })
  }
  handleShow = () => {
    this.setState({
      show: true,
      admin: 'Phan Vn Thnh'
    })
  }
  getDataPrint = (type) => {
    this.setState({selectType: type, printComponent: (<div>ang ti d liu</div>)})
    var data = type === 'table' ? this.state.tableData : this.state.selectedData;
    get_data_print({ type: type, data: data }).then(result => {
      if (result.data.rs === 'success') {
        var printCpn=this.componentPrint(this.printData(result.data.data));
        this.setState({ printComponent: printCpn })
      } else {
        this.setState({printComponent: (<div>C li xy ra</div>)})
      }
    })
  }
  printData = (data) => {
    return (
      <div>
        {data.map((value, index) => {
          return (
              <div key={index}>
                <div style={{height:'50vh'}}>
                  {this.printDetailStructure(value)}
                </div>
                {/* <div style={{height:'100vh'}}>
                  {this.printTableStructure(value)}
                </div> */}
              </div>
          )
        })}
      </div>
    )
  }
  printTableStructure = (data) => {
    var { detail, thongSo } = data
    return (<div className='layout-print'>
      <div>
        {detail.idPhong.loaiPhong.dien && <Row>
          <Col style={{ marginBottom: '10px' }} md={12}><span>* Gi nh mc in sinh hot (bao gm thu GTGT)</span></Col>
          <Col md={12}>
            {this.tableRender(
              thongSo
                .filter(value => value.loaiChiPhi === 0)
                .sort((a, b) => { return a.id > b.id ? 1 : -1 }),
              (<tr>
                <th>STT</th>
                <th>MC S DNG CA MT PHNG TRONG THNG</th>
                <th>THNH TIN</th>
              </tr>)
            )}
          </Col>
        </Row>}

      </div>
      {detail.idPhong.loaiPhong.nuoc && <Row>
        <Col style={{ marginBottom: '10px' }} md={12}><span>* Gi nh mc Nc sinh hot (bao gm thu GTGT v ph bo v mi trng)</span></Col>
        <Col md={12}>
          {this.tableRender(
            thongSo
              .filter(v => v.loaiChiPhi === 1)
              .sort((a, b) => { return a.id > b.id ? 1 : -1 }),
            (<tr style={{ verticalAlign: 'middle' }}>
              <th>STT</th>
              <th>I TNG S DNG NC</th>
              <th>Tng cng <br />(ng/m<sup>3</sup>)</th>
            </tr>)
          )}
        </Col>
      </Row>}
      <Row>
        <Col>
          Tin rc: {detail.idPhong.loaiPhong.tienRac}
        </Col>
      </Row>
    </div>)
  }
  tableRender = (table, header) => {
    return (
      <Table bordered className='table-print'>
        <thead className='text-center'>
          {header}
        </thead>
        <tbody>
          {table && table.map((para, index) => {
            return (
              <tr key={index}>
                <td className='text-center'>{index + 1}</td>
                <td>{para.moTa}</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(para.giaTriThuc)}</td>
              </tr>
            )
          })}
        </tbody>
      </Table>
    )
  }
  dateToString = (date) => {
    return `${date.getDate()}/${date.getMonth()+1}/${date.getFullYear()}`
  }
  printDetailStructure = (data) => {
    // let exp = data.detail
    let exp = data
    let { fromDay, toDay, admin, lastDay } = this.state
    return (<div className={'layout-print'}>
      <Row className='m-b-10'>
        <Col xs={6}>
          <p className='m-b-10 text-center'>TRNG I HC KHOA HC T NHIN</p>
          <p className='m-b-10 text-center bold-style'>PHNG CNG TC SINH VIN</p>
        </Col>
      </Row>
      <Row className={'m-b-10 text-center title-print'}>
        <Col xs={12}> GIY BO IN NC THNG {exp.thang}/{exp.nam}
        </Col>
      </Row>
      <Row className={'m-b-10'}>
        <Col xs={12}>
          Phng: {exp.idPhong.tenPhong} &nbsp;&nbsp; S ngi: {exp.soNguoi}
        </Col>
      </Row>
      <Row>
        <Col xs={12}>
        <div className="table-style-print">
          <Table bordered className='table-print'>
            <thead className="text-center">
              <tr>
                <th>Loi</th>
                <th>Ch s u</th>
                <th>Ch s cui</th>
                <th>Tiu th</th>
                <th>Thnh tin</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>in</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(exp.soDienCu)}</td>
                <td className='text-right m-b-0'>{Optimize.OpitmizeNumber(exp.soDien)}</td>
                <td rowSpan={exp.thayDien ? '2' : '1'} className='text-right vertical-middle'>{
                  Optimize.OpitmizeNumber(exp.thayDien ? exp.soDien - exp.soDienCu + exp.thayDien.dienMoi - exp.thayDien.dienCu :
                    exp.soDien - exp.soDienCu)
                }</td>
                <td rowSpan={exp.thayDien ? '2' : '1'} className='text-right vertical-middle'>{Optimize.OpitmizeNumber(exp.tienDien)}</td>
              </tr>
              {exp.thayDien && <tr>
                <td>in (thay mi)</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(exp.thayDien.dienCu)
                }</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(exp.thayDien.dienMoi)
                }</td>
              </tr>}
              <tr>
                <td>Nc</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(exp.soNuocCu)}</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(exp.soNuoc)
                }</td>
                <td rowSpan={exp.thayNuoc ? '2' : '1'} className='text-right vertical-middle'>{
                  Optimize.OpitmizeNumber(exp.thayNuoc ? exp.soNuoc - exp.soNuocCu + exp.thayNuoc.nuocMoi - exp.thayNuoc.nuocCu :
                    exp.soNuoc - exp.soNuocCu)

                }</td>
                <td rowSpan={exp.thayNuoc ? '2' : '1'} className='text-right vertical-middle'>{Optimize.OpitmizeNumber(exp.tienNuoc)}</td>
              </tr>
              {exp.thayNuoc && <tr>
                <td>Nc (thay mi)</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(exp.thayNuoc.nuocCu)}</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(exp.thayNuoc.nuocMoi)}</td>
              </tr>}
              <tr>
                <td className='text-center' colSpan={4}>Tin rc</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(exp.tienRac)}</td>
              </tr>
              <tr>
                <td className='text-center' colSpan={4}>Tng tin</td>
                <td className='text-right'>{Optimize.OpitmizeNumber(exp.tongTien)}</td>
              </tr>
            </tbody>
          </Table>
          </div>
        </Col>
      </Row>
      <Row className={'m-b-10'}>
        <Col xs={12}>
          Thnh tin: {exp.tongTienChu} ng.
        </Col>
      </Row>
      <Row className={'m-b-10'}>
        <Col xs={6} className={'suggest'}>
          <div style={{ textDecoration: 'underline' }}> ngh:</div>
          <ul className={'suggest-text'}>
            <li>Nu c vn  lin quan n vic cung cp in nc ti cc phng, yu cu sinh vin lin h ti VP.BPQLKTX  gii quyt.</li>
            <li>i din phng mang theo giy bo n VP ng tin</li>
            <li>Thi gian ng tin trc ngy <div className='bold-style' style={{ display: 'inline' }}>{this.dateToString(lastDay)}</div></li>
            <li>Cc phng ghi sai s in, nc tiu th t ngy <div className='bold-style' style={{ display: 'inline' }}>{this.dateToString(fromDay)}</div> n ngy <div className='bold-style' style={{ display: 'inline' }}>{this.dateToString(toDay)}</div> yu cu n lin h ti VP.BP QLKTX <div className='bold-style' style={{ display: 'inline' }}>{`(ng ${admin})`}</div>  chnh sa, qu thi gian trn, VP.BP BPQLKTX s khng chnh sa ch s in nc</li>
            <li>Sau ngy <div className='bold-style' style={{ display: 'inline' }}>{this.dateToString(lastDay)}</div> nhng phng cha ng tin in, nc b phn Qun l KTX s tm ngng cung cp in</li>
          </ul>
        </Col>
        <Col xs={1}></Col>
        <Col xs={5} className={'text-center bold-style'}>
          <div>TP.HCM ngy {fromDay.getDate()} thng {fromDay.getMonth()+1} nm {fromDay.getFullYear()}</div>
          <div>PH B PHN QUN L K TC X</div>
          <p>&nbsp;</p>
          <p>&nbsp;</p>
          <p>&nbsp;</p>
          <div style={{ textTransform: 'uppercase' }}>{admin}</div>
        </Col>
      </Row>
    </div>)
  }
  afterPrint = () => {
    this.handleClose();
  }
  componentPrint = (component) => {
    return (
      <>
        <ReactToPrint
          trigger={() => <Button>In d liu</Button>}
          content={() => this.componentRef}
          onBeforePrint={() => this.afterPrint()}
        />
        <div style={{ display: 'none' }}>
          <ComponentToPrint ref={el => (this.componentRef = el)}>
            {component}
          </ComponentToPrint>
        </div>
      </>
    )
  }
  render() {
    return (
      <React.Fragment>
        <Button onClick={this.handleShow} title="In chi ph"><i className="fas fa-print"></i></Button>
        <Modal show={this.state.show} onHide={this.handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>In chi ph</Modal.Title>
          </Modal.Header>
          <Modal.Body>
          <Row className='m-b-10'>
            <Col md={12}>
            Thi gian chnh sa
            </Col>
						<Col>
						T:
						<div>
							<DatePicker 
								startDate={this.state.fromDay}
								getValue={(date) => {this.setState({fromDay: date})}}
							/>
						</div>
						</Col>
						<Col>
						n:
						<div>
							<DatePicker
								startDate={this.state.toDay}
								getValue={(date) => {this.setState({toDay: date})}}
							/>
						</div>
						</Col>
					</Row>
          <Row>
          <Col>
          ng tin trc ngy:
						<div>
							<DatePicker
								startDate={this.state.lastDay}
								getValue={(date) => {this.setState({lastDay: date})}}
							/>
						</div>
            </Col>
            <Col>
            Ngi qun l
            <Input value={this.state.admin} getValue={t => this.setState({admin: t.value})}/>
            </Col>
          </Row>
            <Row className='m-b-10'>
              <Col>
              <RadioButton name='print-button'
              check={this.state.selectType == 'table'} name={'type'}
                value={'table'}
               label={'In ton bng'} isRadioChk={() => this.getDataPrint('table')} />
                {/* <label className={'print-button'} htmlFor={'ktx-print'} onClick={() => this.getDataPrint('table')}>In ton bng</label> */}
              </Col>
              <Col>
              <RadioButton name='print-button' check={this.state.selectType == 'select'} name={'type'} value={'select'} 
              label={'In  chn'} isRadioChk={() => this.getDataPrint('select')} />
                {/* <label className={'print-button'} htmlFor={'ktx-print'} onClick={() => this.getDataPrint('select')}>In  chn</label> */}
              </Col>
            </Row>
            <Row className='m-b-10'>
              <Col>{this.state.printComponent}</Col>
            </Row>
          </Modal.Body>
          <Modal.Footer>
            <Button variant='default' color='default' onClick={this.handleClose}>
              ng
	          </Button>
          </Modal.Footer>
        </Modal>
      </React.Fragment>
    )
  }
}

export default Confirm