import React from "react";
import { Carousel } from "react-bootstrap";
import './styleHomepage.css'

const Slideshow = () => {
  return (
    <React.Fragment>
      <Carousel>
        <Carousel.Item>
          {/* Single Welcome Slide */}
          <div
            className="single-welcome-slide bg-img bg-overlay"
            style={{ backgroundImage: "url(images/sl1.png)" }}
          >
            {/* Welcome Content */}
            <div className="welcome-content h-100">
              <div className="container h-100">
                <div className="row h-100 align-items-center justify-content-center">
                  {/* Welcome Text */}
                  <div className="col-12 col-md-9 col-lg-6">
                    <div className="welcome-text text-center">
                      <h2 data-animation="fadeInUpBig" data-delay="100ms">
                        Hc tp
                      </h2>
                      <h5 data-animation="fadeInUpBig" data-delay="400ms">
                        Mi trng yn tnh, tp trung
                      </h5>
                     
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          {/* Single Welcome Slide */}
          <div
            className="single-welcome-slide bg-img bg-overlay"
            style={{ backgroundImage: "url(images/sl2.jpg)" }}
          >
            {/* Welcome Content */}
            <div className="welcome-content h-100">
              <div className="container h-100">
                <div className="row h-100 align-items-center justify-content-center">
                  {/* Welcome Text */}
                  <div className="col-12 col-md-9 col-lg-6">
                    <div className="welcome-text text-center">
                      <h2 data-animation="fadeInUpBig" data-delay="100ms">
                       Tin nghi
                      </h2>
                      <h5 data-animation="fadeInUpBig" data-delay="400ms">
                        Nm ngay trung tm thnh ph, thun tin di chuyn
                      </h5>
                     
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Carousel.Item>
        
        <Carousel.Item>
          {/* Single Welcome Slide */}
          <div
            className="single-welcome-slide bg-img bg-overlay"
            style={{ backgroundImage: "url(images/sl3.jpg)" }}
          >
            {/* Welcome Content */}
            <div className="welcome-content h-100">
              <div className="container h-100">
                <div className="row h-100 align-items-center justify-content-center">
                  {/* Welcome Text */}
                  <div className="col-12 col-md-9 col-lg-6">
                    <div className="welcome-text text-center">
                      <h2 data-animation="fadeInUpBig" data-delay="100ms">
                        Rn luyn
                      </h2>
                      <h5 data-animation="fadeInUpBig" data-delay="400ms">
                        Cc hot ng nng cao i sng tinh thn cho sinh vin
                      </h5>
                     
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Carousel.Item>
        
      </Carousel>

    </React.Fragment>
  );
};

export default Slideshow;
